import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const demandData = wutil.makeRandomData({ cnt: 500, min: 1000, max: 4000, onlyUp: false, inputData: [], diff: 100 });
const aiData = wutil.makeRandomData({ cnt: 500, min: 1000, max: 4000, onlyUp: false, demandData, diff: 50, maxSkew: 50 });

const windData = wutil.makeRandomData({ cnt: 500, min: 0, max: 25, onlyUp: false, diff: 1 });
const productionData = wutil.calcData(windData, wutil.calcProductionData, { min: 0, max: 500 });
const productionBoostData = wutil.addPercent(productionData, 10);
const productionLongLifeData = wutil.addPercent(productionData, -5);

const initialState = {
	demandData,
	aiData,
	productionData,
	productionBoostData,
	productionLongLifeData,
};

@observer
class Strategy extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const {
			demandData,
			aiData,
			productionData,
			productionBoostData,
			productionLongLifeData,
		} = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Strategy</h1>
						<LineGraph
							stores={this.props.stores}
							data={demandData}
							dataB={aiData}
							data2={productionData}
							data2B={productionBoostData}
							data2C={productionLongLifeData}
							legend='Demand'
							legendB='AI prediction'
							legend2='Normal production'
							legend2B='Boost 10%'
							legend2C='Long life -5%'
							width={1200}
							height={500}
							yMax={4000}
							yMin={0}
							y2Max={550}
							y2Min={0}
							showXTicks={1}
							showYTicks={1}
							showY2Ticks={1}
							yTicks='["4TW", "3TW", "2TW", "1TW", "0TW"]'
							y2Ticks='["550Mw", "450Mw", "350Mw", "250Mw", "150Mw", "5Mw", "0Mw"]'
							unit='Mw'
							unit2='Mw'
							marker={400}
							avgWindow2={8}
							avgWindow2B={8}
							avgWindow2C={8}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Strategy;
